{\n\t\t#qunit {\n\t\t\tposition: fixed;\n\t\t\tleft: 0;\n\t\t\tright: 0;\n\t\t\ttop: 0;\n\t\t\tbottom: 0;\n\t\t\tpadding: 8px;\n\t\t\tdisplay: -webkit-box;\n\t\t\tdisplay: flex;\n\t\t\tflex-direction: column;\n\t\t}\n\n\t\t#qunit-tests {\n\t\t\toverflow: scroll;\n\t\t}\n\n\t\t#qunit-banner {\n\t\t\tflex: 5px 0 0;\n\t\t}\n\t}\n}\n\n\n/** Header (excluding toolbar) */\n\n#qunit-header {\n\tpadding: 0.5em 0 0.5em 1em;\n\n\tcolor: #C2CCD1;\n\tbackground-color: #0D3349;\n\n\tfont-size: 1.5em;\n\tline-height: 1em;\n\tfont-weight: 400;\n\n\tborder-radius: 5px 5px 0 0;\n}\n\n#qunit-header a {\n\ttext-decoration: none;\n\tcolor: inherit;\n}\n\n#qunit-header a:hover,\n#qunit-header a:focus {\n\tcolor: #FFF;\n}\n\n#qunit-banner {\n\theight: 5px;\n}\n\n#qunit-filteredTest {\n\tpadding: 0.5em 1em 0.5em 1em;\n\tcolor: #366097;\n\tbackground-color: #F4FF77;\n}\n\n#qunit-userAgent {\n\tpadding: 0.5em 1em 0.5em 1em;\n\tcolor: #FFF;\n\tbackground-color: #2B81AF;\n\ttext-shadow: rgba(0, 0, 0, 0.5) 2px 2px 1px;\n}\n\n\n/** Toolbar */\n\n#qunit-testrunner-toolbar {\n\tpadding: 0.5em 1em 0.5em 1em;\n\tcolor: #5E740B;\n\tbackground-color: #EEE;\n}\n\n#qunit-testrunner-toolbar .clearfix {\n\theight: 0;\n\tclear: both;\n}\n\n#qunit-testrunner-toolbar label {\n\tdisplay: inline-block;\n}\n\n#qunit-testrunner-toolbar input[type=checkbox],\n#qunit-testrunner-toolbar input[type=radio] {\n\tmargin: 3px;\n\tvertical-align: -2px;\n}\n\n#qunit-testrunner-toolbar input[type=text] {\n\tbox-sizing: border-box;\n\theight: 1.6em;\n}\n\n#qunit-testrunner-toolbar button,\n#qunit-testresult button {\n  border-radius: .25em;\n  border: 1px solid #AAA;\n  background-color: #F8F8F8;\n  color: #222;\n  line-height: 1.6;\n  cursor: pointer;\n}\n#qunit-testrunner-toolbar button:hover,\n#qunit-testresult button:hover {\n\tborder-color: #AAA;\n\tbackground-color: #FFF;\n\tcolor: #444;\n}\n#qunit-testrunner-toolbar button:active,\n#qunit-testresult button:active {\n\tborder-color: #777;\n\tbackground-color: #CCC;\n\tcolor: #000;\n}\n#qunit-testrunner-toolbar button:focus,\n#qunit-testresult button:focus {\n\tborder-color: #2F68DA;\n\t/* emulate 2px border without a layout shift */\n\tbox-shadow: inset 0 0 0 1px #2F68DA\n}\n#qunit-testrunner-toolbar button:disabled,\n#qunit-testresult button:disabled {\n\tborder-color: #CCC;\n\tbackground-color: #CCC;\n\tcolor: #FFF;\n  cursor: default;\n}\n\n#qunit-toolbar-filters {\n\tfloat: right;\n\t/* aligning right avoids overflows and inefficient use of space\n\t   around the dropdown menu on narrow viewports */\n\ttext-align: right;\n}\n\n.qunit-url-config,\n.qunit-filter,\n#qunit-modulefilter {\n\tdisplay: inline-block;\n\tline-height: 2.1em;\n\ttext-align: left;\n}\n\n.qunit-filter,\n#qunit-modulefilter {\n\tposition: relative;\n\tmargin-left: 1em;\n}\n\n.qunit-url-config label {\n\tmargin-right: 0.5em;\n}\n\n#qunit-modulefilter-search {\n\tbox-sizing: border-box;\n\tmin-width: 400px;\n\tmin-width: min(400px, 80vw);\n}\n\n#qunit-modulefilter-search-container {\n\tposition: relative;\n}\n#qunit-modulefilter-search-container:after {\n\tposition: absolute;\n\tright: 0.3em;\n\tbottom: 0;\n\tline-height: 100%;\n\tcontent: \"\\25bc\";\n\tcolor: black;\n}\n\n#qunit-modulefilter-dropdown {\n\t/* align with #qunit-modulefilter-search */\n\tbox-sizing: border-box;\n\tmin-width: 400px;\n\tmin-width: min(400px, 80vw);\n\tmax-width: 80vw;\n\tposition: absolute;\n\tright: 0;\n\ttop: 100%;\n\tmargin-top: 2px;\n\n\t/* ensure that when on a narrow viewports and having only one result,\n\t   that #qunit-modulefilter-actions fall outside the dropdown rectangle. */\n\tmin-height: 3em;\n\n\tborder: 1px solid #AAA;\n\tborder-top-color: transparent;\n\tborder-radius: 0 0 .25em .25em;\n\tcolor: #0D3349;\n\tbackground-color: #F5F5F5;\n\tz-index: 99;\n}\n\n#qunit-modulefilter-actions {\n\tdisplay: block;\n\toverflow: auto;\n\t/* align with #qunit-modulefilter-dropdown-list */\n\tfont: smaller/1.5em sans-serif;\n}\n@media (min-width: 350px) {\n\t#qunit-modulefilter-actions {\n\t\tposition: absolute;\n\t\tright: 0;\n\t}\n}\n\n#qunit-modulefilter-dropdown #qunit-modulefilter-actions > * {\n\tbox-sizing: border-box;\n\tmax-height: 2.8em;\n\tdisplay: block;\n\tpadding: 0.4em;\n}\n\n#qunit-modulefilter-dropdown #qunit-modulefilter-actions > button {\n\tfloat: right;\n\tmargin: 0.25em;\n}\n\n#qunit-modulefilter-dropdown-list {\n\tmargin: 0;\n\tpadding: 0;\n\tfont: smaller/1.5em sans-serif;\n}\n\n#qunit-modulefilter-dropdown-list li {\n\tlist-style: none;\n}\n#qunit-modulefilter-dropdown-list .clickable {\n\tdisplay: block;\n\tpadding: 0.25em 0.50em 0.25em 0.15em;\n\twhite-space: nowrap;\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n}\n#qunit-modulefilter-dropdown-list .clickable.checked {\n\tfont-weight: bold;\n\tbackground-color: #E2F0F7;\n\tcolor: #0D3349;\n}\n#qunit-modulefilter-dropdown .clickable:hover {\n\tbackground-color: #FFF;\n\tcolor: #444;\n}\n\n\n/** Tests: Pass/Fail */\n\n#qunit-tests {\n\tlist-style-position: inside;\n}\n\n#qunit-tests li {\n\tpadding: 0.4em 1em 0.4em 1em;\n\tborder-bottom: 1px solid #FFF;\n\tlist-style-position: inside;\n}\n\n#qunit-tests > li {\n\tdisplay: none;\n}\n\n#qunit-tests li.running,\n#qunit-tests li.pass,\n#qunit-tests li.fail,\n#qunit-tests li.skipped,\n#qunit-tests li.aborted {\n\tdisplay: list-item;\n}\n\n#qunit-tests.hidepass {\n\tposition: relative;\n}\n\n#qunit-tests.hidepass li.running,\n#qunit-tests.hidepass li.pass:not(.todo) {\n\tvisibility: hidden;\n\tposition: absolute;\n\twidth:   0;\n\theight:  0;\n\tpadding: 0;\n\tborder:  0;\n\tmargin:  0;\n}\n\n#qunit-tests li strong {\n\tcursor: pointer;\n}\n\n#qunit-tests li.skipped strong {\n\tcursor: default;\n}\n\n#qunit-tests li a {\n\tpadding: 0.5em;\n\tcolor: inherit;\n\ttext-decoration: underline;\n}\n#qunit-tests li a:hover,\n#qunit-tests li a:focus {\n\tcolor: #0D3349;\n}\n\n#qunit-tests li .runtime {\n\tfloat: right;\n\tfont-size: smaller;\n}\n\n.qunit-assert-list {\n\tmargin-top: 0.5em;\n\tpadding: 0.5em;\n\n\tbackground-color: #FFF;\n\n\tborder-radius: 5px;\n}\n\n.qunit-source {\n\tmargin: 0.6em 0 0.3em;\n}\n\n.qunit-collapsed {\n\tdisplay: none;\n}\n\n#qunit-tests table {\n\tborder-collapse: collapse;\n\tmargin-top: 0.2em;\n}\n\n#qunit-tests th {\n\ttext-align: right;\n\tvertical-align: top;\n\tpadding: 0 0.5em 0 0;\n}\n\n#qunit-tests td {\n\tvertical-align: top;\n}\n\n#qunit-tests pre {\n\tmargin: 0;\n\twhite-space: pre-wrap;\n\tword-wrap: break-word;\n}\n\n#qunit-tests del {\n\tcolor: #374E0C;\n\tbackground-color: #E0F2BE;\n\ttext-decoration: none;\n}\n\n#qunit-tests ins {\n\tcolor: #500;\n\tbackground-color: #FFCACA;\n\ttext-decoration: none;\n}\n\n/*** Test Counts */\n\n#qunit-tests b.counts                       { color: #0D3349; }\n#qunit-tests b.passed                       { color: #5E740B; }\n#qunit-tests b.failed                       { color: #710909; }\n\n#qunit-tests li li {\n\tpadding: 5px;\n\tbackground-color: #FFF;\n\tborder-bottom: none;\n\tlist-style-position: inside;\n}\n\n/*** Passing Styles */\n\n\n#qunit-tests .pass {\n\tcolor: #2F68DA;\n\tbackground-color: #E2F0F7;\n}\n\n#qunit-tests .pass .test-name {\n\tcolor: #366097;\n}\n\n#qunit-tests li li.pass {\n\tcolor: #3C510C;\n\tbackground-color: #FFF;\n\tborder-left: 10px solid #C6E746;\n}\n\n#qunit-tests .pass .test-actual,\n#qunit-tests .pass .test-expected           { color: #999; }\n\n#qunit-banner.qunit-pass                    { background-color: #C6E746; }\n\n/*** Failing Styles */\n\n#qunit-tests .fail {\n\tcolor: #000;\n\tbackground-color: #EE5757;\n}\n\n#qunit-tests li li.fail {\n\tcolor: #710909;\n\tbackground-color: #FFF;\n\tborder-left: 10px solid #EE5757;\n\twhite-space: pre;\n}\n\n#qunit-tests > li:last-child {\n\tborder-radius: 0 0 5px 5px;\n}\n\n#qunit-tests .fail .test-actual             { color: #EE5757; }\n#qunit-tests .fail .test-expected           { color: #008000; }\n\n#qunit-banner.qunit-fail                    { background-color: #EE5757; }\n\n\n/*** Aborted tests */\n#qunit-tests .aborted { color: #000; background-color: orange; }\n/*** Skipped tests */\n\n#qunit-tests .skipped {\n\tbackground-color: #EBECE9;\n}\n\n#qunit-tests .qunit-todo-label,\n#qunit-tests .qunit-skipped-label {\n\tbackground-color: #F4FF77;\n\tdisplay: inline-block;\n\tfont-style: normal;\n\tcolor: #366097;\n\tline-height: 1.8em;\n\tpadding: 0 0.5em;\n\tmargin: -0.4em 0.4em -0.4em 0;\n}\n\n#qunit-tests .qunit-todo-label {\n\tbackground-color: #EEE;\n}\n\n/** Result */\n\n#qunit-testresult {\n\tcolor: #366097;\n\tbackground-color: #E2F0F7;\n\n\tborder-bottom: 1px solid #FFF;\n}\n#qunit-testresult a {\n\tcolor: #2F68DA;\n}\n#qunit-testresult .clearfix {\n\theight: 0;\n\tclear: both;\n}\n#qunit-testresult .module-name {\n\tfont-weight: 700;\n}\n#qunit-testresult-display {\n\tpadding: 0.5em 1em 0.5em 1em;\n\twidth: 85%;\n\tfloat:left;\n}\n#qunit-testresult-controls {\n\tpadding: 0.5em 1em 0.5em 1em;\n  width: 10%;\n\tfloat:left;\n}\n\n/** Fixture */\n\n#qunit-fixture {\n\tposition: absolute;\n\ttop: -10000px;\n\tleft: -10000px;\n\twidth: 1000px;\n\theight: 1000px;\n}\n"],"sourceRoot":""}]);
